@import '../../scss/settings/colors';
@import '../../scss/settings/mixins';
@import '../../scss/settings/colors';

.footer-conainer {
  width: 100%;
  height: 7rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-size: 1.2rem;
  color: $fontColor;
  .column {
    width: 33%;
    margin: 1rem 0;
    text-align: center;
    line-height: 1.5rem;
    a {
      text-decoration: none;
      color: $fontColor;
    }
    .fab {
      margin-bottom: 1rem;
      color: #3b5998;
    }
  }
}

@include mobile {
  .footer-conainer {
    height: 18rem;
    flex-direction: column;
    .column {
      width: 100%;
    }
  }
}
